import {MDCToolbar} from '@material/toolbar';
import {MDCTabBar} from '@material/tabs';
import {MDCTemporaryDrawer} from '@material/drawer';
import navTemplate from './nav.html';
import './nav.scss';

let mainHeader = document.querySelector('#main-header');
mainHeader.innerHTML = navTemplate;

let toolBar = MDCToolbar.attachTo(document.querySelector('.mdc-toolbar'));
toolBar.fixedAdjustElement = document.querySelector('.mdc-toolbar-fixed-adjust');

let drawer = new MDCTemporaryDrawer(document.querySelector('.mdc-drawer--temporary'));
document.querySelector('#side-nav-toggle').addEventListener('click', () => drawer.open = true);

let tabBar = MDCTabBar.attachTo(document.querySelector('#main-nav-tab-bar'));

// Add bottom shadow to big navBar on larger screens when window scrolls down 25px.
let shouldAddBottomShadow = true;
let bigNav = document.querySelector('.big-nav');
window.addEventListener('scroll', ()=>{
    if (document.body.scrollTop > 25 || document.documentElement.scrollTop > 25){
        if(shouldAddBottomShadow){
            bigNav.className += ' nav-bottom-shadow';
            shouldAddBottomShadow = false;
        }            
    }else{
        bigNav.classList.remove("nav-bottom-shadow");
        shouldAddBottomShadow = true;
    }
})

